@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap');

body {
    font-family: 'Roboto', sans-serif;
    word-wrap: break-word;
}

.center {
    display: block;
    text-align: center;
    margin-top: 20px;
}

.stick {
    position: sticky;
    position: -webkit-sticky;
    top: 0;
}

.temp {
    padding-top: 1300px;
    color: black;
}

.profile {
    max-height: 300px;
    display: block;
    margin: 40px auto 0 auto;
}

hr.bar {
    border-top: 1px solid white;
    width: 80%;
}

hr.separator {
    border-top: 1px solid black;
    width: 100%;
}

.firstName {
    font-weight: 100;
}

.name {
    font-size: 40px;
    text-align: center;
    margin-top: 10px;
}

.button {
    border-radius: 50%;
    margin: 10px 20px 10px 0px;
}

.side {
    min-height: 100% !important;
    height: 100vh;
    color: white;
    position: relative;
}

.main {
    padding: 60px;
    background: white;
}

.section {
    margin-top: 10px;
    padding: 20px;
}